#clock {
    margin: 0 auto auto auto; 
}

#timer-label {
    font-size: 50px; 
    font-weight: bold;
    letter-spacing: 20px; 
    text-transform: uppercase;
    margin-bottom: 10px; 
    background-color: var(--white);
    border-radius: 5px;
}

#time-left {
    height: 250px;
    width: 500px; 
    background-color: var(--white);
    border-radius: 35px;
    font-size: 170px;
    display: flex; 
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px; 
}

#clock-controllers {
    display: flex; 
    flex-direction: row; 
    justify-content: space-around;
}

#start_stop, #reset {
    background-color: var(--white);
    border-radius: 35px; 
    color: var(--text-color); 
    height: 8vh; 
    width: 10vw; 
    font-size: 25px; 
}


