#controllers {
    grid-column: 2 / -1; 
    grid-row: 2 / -1; 
    display: flex; 
    flex-direction: column; 
    justify-content: space-around;
    align-items: flex-start;
    padding: 11vh 0 22vh 0; 
}


#break, #session {
    width: 150px; 
}

#break-label, #session-label {
    font-size: 30px;
    font-weight: 100;
    letter-spacing: 1px; 
    background-color: var(--white);
    border-radius: 5px;
}

.controlls {
    margin-top: 5px; 
    height: auto;
    display: flex; 
    flex-direction: row; 
    justify-content: space-between;
    align-items: center;
    background-color: var(--white); 
    border-radius: 35px 0 0 35px;
}

#break-length, #session-length {
    font-size: 30px;
    margin-left: 25%; 
}

.increment-decrement {
    width: 30%; 
    display: flex; 
    flex-direction: column;
}

#break-increment, #break-decrement, #session-increment, #session-decrement {
    width: 100%;
    height: 20px;
    font-size: 10px;
    color: var(--white); 
}

